$(document).ready(function(){
  const headHeight = 1064;
  let ua = navigator.userAgent;
  let ipad = ua.match(/(iPad).*OS\s([\d_]+)/),
    isIphone =!ipad && ua.match(/(iPhone\sOS)\s([\d_]+)/),
    isAndroid = ua.match(/(Android)\s+([\d.]+)/),
    isMobile = isIphone || isAndroid;
  let screenWidth = document.documentElement.clientWidth||document.documentElement.clientWidth||document.documentElement.clientWidth;
  if(isMobile){
    $('#app').css('width',screenWidth);
  }else{
    set(headHeight);
    $(window).resize(function(){
      set(headHeight);
    })
  }
})
function set(headHeight) {
  let screenWidth = document.documentElement.clientWidth||document.documentElement.clientWidth||document.documentElement.clientWidth;
  let headAuto = autoHeight(screenWidth,1064);
  let tigerAuto = autoHeight(screenWidth,1300);
  if(screenWidth>=1200){
    $('#app').css('width',screenWidth);
    $('.head').css('height',headAuto);
    $('.tigerAuto').css('height',tigerAuto);
    setHtmlFontSize(screenWidth);
  }
}
function setHtmlFontSize(screenWidth) {
  if(screenWidth>=1200){
    $('html').css('font-size',screenWidth/100);
  }
}
function autoHeight(screenWidth,height){
  return (screenWidth/1920)*height;
}



