// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
// import router from './router'
import VueRouter from 'vue-router'
import first from './components/home.vue'
import second from './components/index.vue'
import Vuex from 'vuex'

Vue.config.productionTip = false
Vue.use(VueRouter)
Vue.use(Vuex)

const router = new VueRouter({
  mode: 'history',
  base: __dirname,
  routes: [{
    path: '/first',
    component: first
  }, {
    path: '/second',
    component: second
  }
  ]
})
new Vue({
  router: router,
  render: h => h(App)
}).$mount('#app')



