<template>
    <div id="phone">
        <p>tel:</p>
        <span>{{tel}}</span>
      <br>
        <input type="button" @click="n +=1" v-model="n">
      <br>
      <span>{{qq}}</span>
      <span>{{email}}</span>
    </div>
</template>

<script>
export default{
  data () {
    return {
      tel: '123456',
      n: 0
    }
  },
  props: ['qq', 'email']
}
</script>
<style>
#phone{
  border: 2px solid #555;
  margin-top: 5px;
}
</style>



