        return number % 2 === 0
      })
    },
    subtn: function () {
      if (this.goodsname != '') { alert(this.goodsname) }
    }
  },
  computed: {
    qqr: function () {
      return this.qq.split('').reverse().join('')
    },
    lastname: function () {
      return this.name1 + ' ' + this.name2
    },
    evenNub: function () {
      return this.numbers.filter(function (number) {
        return number % 2 === 0
      })
    }
